:root {
	--MainBGColor: #fff;
	--fontColor: #4d5066;
	--topImage: url("images/bg-desktop-light.jpg");
	--taskBGColor: #ffffff;
	--shadow: 0 0 10px #a4a4a459;
	--shadowPhone: 0 3px 6px 0 #a4a4a459;
	--borderColor: #d2d3db;
	--taskLineColor: #e5e4e9;
	--inputColor: #9394a5;
	--inputColorTasks: #484b6a;
	--completedColor: #d2d3db;
	--infoColor: #9f9ea3;
	--filterColor: #9394a5;
	--filterHover: #484b6a;
	--completedHover: #4d5066;
}

[data-theme="dark"] {
	--MainBGColor: #181824;
	--topImage: url("images/bg-desktop-dark.jpg");
	--taskBGColor: #25273c;
	--shadow: none;
	--shadowPhone: none;
	--borderColor: #393a4c;
	--taskLineColor: #37394e;
	--inputColor: #cacde8;
	--inputColorTasks: var(--inputColor);
	--completedColor: #777a92;
	--infoColor: #4d5066;
	--filterColor: #5f607e;
	--filterHover: #e4e5f1;
	--completedHover: var(--filterHover);
}

* {
	padding: 0;
	margin: 0;
	border: none;
	box-sizing: border-box;
	font-family: "Josefin Sans", sans-serif;
	transition: all 250ms;
	list-style: none;
}

body {
	width: 100%;
	height: 100vh;
	background: var(--MainBGColor) var(--topImage) top no-repeat;
	background-size: contain;
	background-attachment: fixed;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
	padding-top: 74px;
}

div[class^="row"],
form[class^="row"] {
	display: flex;
	align-items: center;
}

.row1 {
	padding-bottom: 37px;
	justify-content: space-between;
}

@keyframes changeMode {
	50% {
		transform: rotate(90deg);
	}
	100% {
		transform: rotate(0);
	}
}

.animateDarkMode {
	animation: changeMode 500ms ease-in-out;
}

.mainH1 {
	font-weight: bold;
	font-size: 41px;
	line-height: 115%;
	letter-spacing: 0.34em;
	color: #ffffff;
}

main {
	width: min(87.2%, 540px);
}

.addTask {
	gap: 24px;
	height: 64px;
	background-color: var(--taskBGColor);
	border-radius: 5px;
	padding: 0 24px;
	box-shadow: var(--shadow);
}

.addTask:not(.allTasks) {
	margin-bottom: 24px;
}

input[type="checkbox"],
input[type="radio"] {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

.checkmark {
	width: 24px;
	height: 24px;
	border: 2px solid var(--borderColor);
	background: transparent;
	border-radius: 50%;
	cursor: pointer;
	display: grid;
	place-items: center;
}

.checkmark::after {
	content: url("images/icon-check.svg ");
	display: none;
}

input[type="checkbox"]:checked ~ .checkmark {
	border: none;
	background: linear-gradient(141.52deg, #57ddff 14.09%, #c058f3 82.28%);
}

input[type="checkbox"]:checked ~ .checkmark::after {
	display: block;
}

.completed {
	color: var(--completedColor) !important;
	text-decoration: line-through;
}

.removeTask {
	cursor: pointer;
}

.task form {
	width: 100%;
}

input[type="text"] {
	cursor: pointer;
	width: 100%;
	caret-color: #3a7bfd;
	margin-top: 5px;
	outline: none;
	background-color: transparent;
	font-size: 18px;
	line-height: 115%;
	letter-spacing: -0.015em;
	color: var(--inputColorTasks);
}
input[type="text"]::placeholder {
	color: var(--inputColor);
}

.allTasks {
	background-color: var(--taskBGColor);
	border-radius: 5px;
	box-shadow: var(--shadow);
}

.task {
	gap: 24px;
	padding: 21px 24px;
	border-bottom: 1px solid var(--taskLineColor);
	background-color: var(--taskBGColor);
}

.task:active {
	border-radius: 10px;
}

.bottomNav {
	padding: 17px 24px 15px;
	justify-content: space-between;
}

.leftItems {
	font-size: 15px;
	line-height: 115%;
	letter-spacing: -0.04em;
	color: var(--filterColor);
	cursor: context-menu;
}

.filters {
	justify-content: space-between;
	width: 165px;
}

.filters p {
	cursor: pointer;
	font-weight: 700;
	letter-spacing: -0.025em;
	font-size: 14px;
	line-height: 115%;
	color: var(--filterColor);
}
.filters p:hover {
	color: var(--filterHover);
}

input[type="radio"]:checked ~ p {
	color: #3a7bfd;
}

#clearCompleted {
	cursor: pointer;
	background-color: transparent;
	color: var(--filterColor);
}

#clearCompleted:hover {
	color: var(--completedHover);
}

.info {
	margin-bottom: 6vh;
	color: var(--infoColor);
	font-size: 14px;
}

@media screen and (max-width: 800px) {
	:root {
		--topImage: url("images/bg-mobile-light.jpg");
	}
	[data-theme="dark"] {
		--topImage: url("images/bg-mobile-dark.jpg");
	}

	body {
		min-width: 300px;
	}

	.mainH1 {
		font-size: 27px;
		letter-spacing: 0.34em;
	}

	.row1 {
		padding-bottom: 31px;
	}

	input[type="text"] {
		font-size: 15px;
	}

	.bottomNav {
		background-color: var(--taskBGColor);
		border-radius: 0 0 5px 5px;
		box-shadow: var(--shadowPhone);
	}

	.filters {
		border-radius: 5px;
		margin-top: 18px;
		padding: 18px 10%;
		display: flex;
		justify-content: space-around;
		width: 100%;
		background-color: var(--taskBGColor);
		box-shadow: var(--shadowPhone);
	}
}
